@import "../../styles/variables";

.passwordContainer {
  max-width: 32rem;
  margin: $size-10 auto 0 auto;
  text-align: center;
  border-radius: $border-radius-large;
  line-height: normal;
  @include tile-box-shadow;
  @include themed() {
    border: 1px solid t($color-icon);
    background-color: t($color-background-light);
  }
  padding: $size-8;
  @media screen and (min-width: $breakpoint-sm) {
    padding: $size-8 $size-9;
  }

  .passwordIcon {
    width: auto;
    height: $size-7;
    margin-bottom: $size-5;
    @include themed() {
      color: t($color-icon);
    }
  }

  .formContainer {
    width: fit-content;
    margin: $size-7 auto 0 auto;
  }

  .passwordForm {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: $size-2;

    .passwordInput {
      @include body-2;
      width: 12rem;
      padding: $size-1;
      border-top: none;
      border-right: none;
      border-left: none;
      outline: none;
      margin-right: $size-3;
      transition: border-color $transition-color;
      @include themed() {
        color: t($color-text);
        border-bottom: 2px solid t($color-button);
        background-color: t($color-background-light);
      }

      &:focus {
        @include themed() {
          border-color: t($color-button-hover);
        }
      }
    }

    .passwordButton {
      @include button-style;
    }
  }

  .passwordError {
    display: flex;
    align-items: center;
    justify-content: center;
    font-style: italic;

    .passwordWarning {
      height: $size-3;
      width: auto;
      margin-right: $size-1;
    }
  }
}
